.App {
  margin: 40px; }

.demo-list-item-body {
  background: #fff;
  border: 1px solid #e6e7eb;
  box-shadow: inset 0 0 0 0 #e6e7eb;
  border-radius: 4px;
  padding: 20px; }

.basic-nav .fusion {
  margin: 8px 20px;
  width: 100px;
  color: #FFFFFF;
  font-size: 20px; }

.basic-nav .login-in {
  margin: 0 20px;
  color: #FFFFFF; }

.basic-nav .next-nav-item {
  width: 120px; }

.marginTop12 {
  margin-top: 12px; }

.app,
app-dashboard,
app-root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.app-header {
  position: fixed;
  z-index: 1020;
  flex: 0 0 55px;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 55px;
  background-color: #F5FAFF;
  border-bottom: 1px #e6e7e7 solid;
  -webkit-box-shadow: 2px 4px 8px #e6e7e7; }

.app-header .container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center; }

.app-footer {
  flex: 0 0 50px; }

.app-body {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  overflow-x: hidden;
  margin-top: 55px; }
  .app-body .main {
    flex: 1;
    min-width: 0;
    padding: 24px; }
  .app-body .sidebar {
    position: fixed;
    z-index: 1021;
    width: 200px;
    top: 55px;
    left: 0;
    bottom: 0;
    overflow-y: auto; }
  .app-body .aside-menu {
    flex: 0 0 250px; }

/*滚动条样式*/
.sidebar::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 0px; }

.sidebar::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2); }

.sidebar::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0);
  border-radius: 0;
  background: rgba(0, 0, 0, 0); }

.app .logo {
  width: 55px;
  height: 55px; }

